#sketchpage {
    width: 100%;
    height: 94.2%;
}

#svg {
    width: 100%;
    height: 100%;
}

.cursor {
    width: 4px;
    height: 4px;
    border-radius: 50%;
    border: 1px solid #000000;
    box-shadow:
    0 0 0 1px hsl(0, 0%, 100%);
    position: absolute;
    pointer-events: none;
    transform: translate(-50%, -50%);
}