#topbar {
    text-align: left;
    background-color: rgb(250,250,250);
}

.float-btn {
    float: left;
}

.stroke-size-slider {
    width: "14px";
    padding-left: 12px;
    padding-right: 12px;
}

label.import-label input[type="file"] {
    position:absolute;
    top: -1000px;
}

.import-label {
    float: left;
    color: rgb(245,0,87);
    padding: 10px;
    padding-left: 12px;
    padding-right: 12px;
    border-radius: 50%;
}

.import-label:hover {
    background: rgb(250,241,244);
    cursor: pointer;
}

#eraser-background {
    /* insanely, material-ui doesn't have an eraser icon */
    content: url('https://api.iconify.design/bi/eraser.svg?color=grey');
    vertical-align: -0.125em;
}
